// src/app.ts
import { AstroController } from './astro-controller';
import { AstroViewerAdapter } from './astroviewer-adapter'; // suppongo esporti un adapter IAstroViewerAPI
import { UIPanelManager } from './ui/UIPanelManager';
import { bus } from './bus';
function main() {
    // 1) Inizializza AstroViewer + Controller
    const api = new AstroViewerAdapter(); // adatta a come lo istanzi
    api.init();
    const controller = new AstroController(api);
    controller.mount();
    // 2) UI: area di lavoro + manager pannelli
    const workarea = document.getElementById('workarea')
        ?? document.body; // fallback se non hai un div dedicato
    const ui = new UIPanelManager(workarea);
    ui.mount();
    // 3) (opzionale) pannello iniziale
    bus.emit('ui:openPanel', { tab: 'explore' });
}
main();
